.datatable {
    height: 600px;
    padding: 20px;

    .cellWithImg {
        display: flex;
        align-items: center;

        .cellImg {
            width: 32px;
            height: 32px;
            border-radius: 50%;
            object-fit: cover;
            margin-right: 20px;
        }
    }

    .cellWithStatus {
        padding: 5px;
        border-radius: 5px;

        &.active {
            background-color: rgba(0, 128, 0, 0.05);
            color: green;
        }

        &.pending {
            background-color: rgba(255, 217, 0, 0.05);
            color: goldenrod;
        }

        &.passive {
            background-color: rgba(255, 0, 0, 0.05);
            color: crimson;
        }
    }

    .cellAction {
        display: flex;
        align-items: center;
        gap: 15px;

        .viewButton {
            padding: 2px 5px;
            border-radius: 5px;
            border: 1px solid rgba(0, 0, 139, 0.575);
            color: darkblue;
            cursor: pointer;
            &:hover{
                padding: 2px 5px;
                border-radius: 5px;
                border: 1px solid rgba(0, 0, 139, 0.992);
                color: rgba(0, 0, 139, 0.73);
                cursor: pointer;
            }
        }

        .deleteButton {
            padding: 2px 5px;
            border-radius: 5px;
            border: 1px solid rgba(220, 20, 60, 0.6);
            color: crimson;
            cursor: pointer;
            &:hover{
                padding: 2px 5px;
                border-radius: 5px;
                border: 1px solid rgba(220, 20, 60, 0.957);
                color: rgba(220, 20, 60, 0.808);
                cursor: pointer;
            }
        }
    }
}